/* Modern Premium ProductCard Design */

.product-card {
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  background: var(--gradient-glass);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-lg);
  color: var(--color-text-primary);
  overflow: hidden;
  position: relative;
}

.product-card:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: var(--shadow-xl), var(--shadow-glow);
  border-color: rgba(255, 255, 255, 0.2);
}

.product-card .card-title {
  color: var(--color-text-primary) !important;
  font-weight: 700;
  line-height: 1.3;
  font-size: 1.1rem;
  margin-bottom: var(--spacing-sm);
}

.product-card .card-body {
  color: var(--color-text-primary) !important;
  background: transparent !important;
  padding: var(--spacing-md);
}

.product-card .price-section {
  color: var(--color-text-primary) !important;
  margin: var(--spacing-sm) 0;
}

.product-card .price {
  color: var(--color-primary) !important;
  font-weight: 800;
  font-size: 1.25rem;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent !important;
}

.product-card .cutted-price {
  color: var(--color-text-muted);
  text-decoration: line-through;
  font-size: 0.9rem;
  margin-left: var(--spacing-xs);
}

.product-card .discount-badge {
  position: absolute;
  top: var(--spacing-sm);
  left: var(--spacing-sm);
  background: var(--gradient-primary);
  color: white;
  padding: 0.25rem 0.75rem;
  border-radius: var(--radius-sm);
  font-size: 0.75rem;
  font-weight: 700;
  z-index: 2;
  box-shadow: var(--shadow-md);
}

.product-card .out-of-stock-badge {
  position: absolute;
  top: var(--spacing-sm);
  right: var(--spacing-sm);
  background: rgba(239, 68, 68, 0.9);
  color: white;
  padding: 0.25rem 0.75rem;
  border-radius: var(--radius-sm);
  font-size: 0.75rem;
  font-weight: 700;
  z-index: 2;
  box-shadow: var(--shadow-md);
}

.product-card .category-badge {
  position: absolute;
  bottom: var(--spacing-sm);
  left: var(--spacing-sm);
  background: rgba(99, 102, 241, 0.9);
  color: white;
  padding: 0.25rem 0.75rem;
  border-radius: var(--radius-sm);
  font-size: 0.75rem;
  font-weight: 700;
  z-index: 2;
  box-shadow: var(--shadow-md);
}

.product-card .wishlist-btn {
  position: absolute;
  top: var(--spacing-sm);
  right: var(--spacing-sm);
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-danger);
  font-size: 1.1rem;
  z-index: 3;
  transition: all 0.3s ease;
  cursor: pointer;
}

.product-card .wishlist-btn:hover {
  background: var(--color-danger);
  color: white;
  transform: scale(1.1);
  box-shadow: var(--shadow-glow);
}

.product-card .quick-view-overlay {
  position: absolute;
  inset: 0;
  background: rgba(15, 23, 42, 0.9);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: 4;
  border-radius: var(--radius-lg);
}

.product-card:hover .quick-view-overlay {
  opacity: 1;
}

.product-card .quick-view-btn {
  background: var(--gradient-primary);
  color: white;
  border: none;
  padding: var(--spacing-sm) var(--spacing-md);
  border-radius: var(--radius-md);
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: var(--shadow-md);
}

.product-card .quick-view-btn:hover {
  transform: scale(1.05);
  box-shadow: var(--shadow-lg), var(--shadow-glow);
}

.product-card .image-container {
  position: relative;
  overflow: hidden;
  border-radius: var(--radius-lg);
  background: var(--color-bg-secondary);
  height: 240px;
  margin: var(--spacing-sm);
}

.product-card .image-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.product-card:hover .image-container img {
  transform: scale(1.1);
}

.product-card .variant-select {
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-radius: var(--radius-md);
  padding: var(--spacing-sm);
  background: rgba(255, 255, 255, 0.05);
  color: var(--color-text-primary);
  font-size: 0.875rem;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  width: 100%;
  margin-bottom: var(--spacing-sm);
}

.product-card .variant-select:focus {
  border-color: var(--color-primary);
  outline: none;
  box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.1);
  background: rgba(255, 255, 255, 0.1);
}

.product-card .add-to-cart-btn {
  background: var(--gradient-primary);
  border: none;
  color: white;
  font-weight: 700;
  padding: var(--spacing-sm) var(--spacing-md);
  border-radius: var(--radius-md);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  width: 100%;
  margin-top: var(--spacing-sm);
  box-shadow: var(--shadow-md);
}

.product-card .add-to-cart-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg), var(--shadow-glow);
}

.product-card .add-to-cart-btn:disabled {
  background: var(--color-text-muted);
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.product-card .add-to-cart-btn.loading {
  pointer-events: none;
}

.product-card .add-to-cart-btn.loading::after {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.3), transparent);
  animation: shimmer 1.5s infinite;
}

@keyframes shimmer {
  0% { left: -100%; }
  100% { left: 100%; }
}

.product-card .rating-stars {
  color: var(--color-warning);
  font-size: 1rem;
  margin-bottom: var(--spacing-xs);
}

.product-card .rating-text {
  color: var(--color-text-muted);
  font-size: 0.875rem;
}

/* Loading skeleton */
.product-card.loading .image-container {
  background: linear-gradient(90deg, var(--color-bg-secondary) 25%, var(--color-bg-tertiary) 50%, var(--color-bg-secondary) 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
}

@keyframes loading {
  0% { background-position: 200% 0; }
  100% { background-position: -200% 0; }
}

/* Responsive Design */
@media (max-width: 768px) {
  .product-card .image-container {
    height: 200px;
  }
  
  .product-card .card-body {
    padding: var(--spacing-sm);
  }
  
  .product-card .card-title {
    font-size: 1rem;
  }
  
  .product-card .add-to-cart-btn {
    padding: var(--spacing-sm);
    font-size: 0.875rem;
  }
  
  .product-card .variant-select {
    font-size: 0.8rem;
    padding: var(--spacing-xs);
  }
}

@media (max-width: 576px) {
  .product-card .image-container {
    height: 180px;
  }
  
  .product-card .card-body {
    padding: var(--spacing-xs);
  }
  
  .product-card .card-title {
    font-size: 0.9rem;
  }
  
  .product-card .add-to-cart-btn {
    padding: var(--spacing-xs);
    font-size: 0.8rem;
  }
  
  .product-card .wishlist-btn {
    width: 36px;
    height: 36px;
    font-size: 1rem;
  }
  
  .product-card .discount-badge,
  .product-card .out-of-stock-badge,
  .product-card .category-badge {
    font-size: 0.7rem;
    padding: 0.2rem 0.5rem;
  }
} 