/* Modern React Ecommerce Theme - Premium Design */
:root {
  /* Premium Color Palette */
  --color-primary: #6366F1;       /* Indigo */
  --color-secondary: #0EA5E9;     /* Sky Blue */
  --color-accent: #8B5CF6;        /* Purple */
  --color-success: #10B981;       /* Emerald */
  --color-warning: #F59E0B;       /* Amber */
  --color-danger: #EF4444;        /* Red */
  
  /* Background Colors */
  --color-bg-primary: #0F172A;    /* Dark Blue */
  --color-bg-secondary: #1E293B;  /* Slate */
  --color-bg-tertiary: #334155;   /* Slate 600 */
  --color-bg-glass: rgba(255, 255, 255, 0.1);
  
  /* Text Colors */
  --color-text-primary: #F8FAFC;  /* Slate 50 */
  --color-text-secondary: #CBD5E1; /* Slate 300 */
  --color-text-muted: #64748B;    /* Slate 500 */
  --color-text-dark: #1E293B;     /* Slate 800 */
  
  /* Gradients */
  --gradient-primary: linear-gradient(135deg, #6366F1 0%, #8B5CF6 50%, #0EA5E9 100%);
  --gradient-secondary: linear-gradient(135deg, #0EA5E9 0%, #10B981 50%, #F59E0B 100%);
  --gradient-glass: linear-gradient(135deg, rgba(255,255,255,0.1) 0%, rgba(255,255,255,0.05) 100%);
  
  /* Shadows */
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  --shadow-glow: 0 0 20px rgba(99, 102, 241, 0.3);
  
  /* Border Radius */
  --radius-sm: 0.5rem;
  --radius-md: 1rem;
  --radius-lg: 1.5rem;
  --radius-xl: 2rem;
  
  /* Spacing */
  --spacing-xs: 0.5rem;
  --spacing-sm: 1rem;
  --spacing-md: 1.5rem;
  --spacing-lg: 2rem;
  --spacing-xl: 3rem;
}

/* Global Styles */
* {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background: var(--color-bg-primary);
  color: var(--color-text-primary);
  line-height: 1.6;
  overflow-x: hidden;
}

body {
  background: 
    radial-gradient(ellipse at top, rgba(99, 102, 241, 0.1) 0%, transparent 50%),
    radial-gradient(ellipse at bottom, rgba(14, 165, 233, 0.1) 0%, transparent 50%),
    linear-gradient(180deg, var(--color-bg-primary) 0%, var(--color-bg-secondary) 100%);
  min-height: 100vh;
}

/* Typography */
h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  line-height: 1.2;
  margin: 0 0 var(--spacing-sm) 0;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

h1 { font-size: 3rem; }
h2 { font-size: 2.5rem; }
h3 { font-size: 2rem; }
h4 { font-size: 1.5rem; }
h5 { font-size: 1.25rem; }
h6 { font-size: 1rem; }

p {
  color: var(--color-text-secondary);
  margin: 0 0 var(--spacing-sm) 0;
}

/* Glass Morphism Components */
.glass-card {
  background: var(--gradient-glass);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-lg);
  transition: all 0.3s ease;
}

.glass-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-xl), var(--shadow-glow);
  border-color: rgba(255, 255, 255, 0.2);
}

/* Buttons */
.btn {
  border: none;
  border-radius: var(--radius-md);
  padding: var(--spacing-sm) var(--spacing-md);
  font-weight: 600;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.btn-primary {
  background: var(--gradient-primary);
  color: white;
  box-shadow: var(--shadow-md);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg), var(--shadow-glow);
}

.btn-secondary {
  background: var(--gradient-secondary);
  color: white;
  box-shadow: var(--shadow-md);
}

.btn-secondary:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.btn-outline {
  background: transparent;
  border: 2px solid var(--color-primary);
  color: var(--color-primary);
}

.btn-outline:hover {
  background: var(--color-primary);
  color: white;
  transform: translateY(-2px);
}

/* Cards */
.card {
  background: var(--gradient-glass);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-lg);
  transition: all 0.3s ease;
  overflow: hidden;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-xl), var(--shadow-glow);
}

.card-header {
  background: var(--gradient-primary);
  color: white;
  padding: var(--spacing-md);
  border: none;
}

.card-body {
  padding: var(--spacing-md);
  color: var(--color-text-primary);
}

/* Navigation */
.navbar {
  background: rgba(15, 23, 42, 0.8);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: var(--shadow-md);
}

.navbar-brand {
  color: var(--color-text-primary) !important;
  font-weight: 700;
  font-size: 1.5rem;
}

.navbar-nav .nav-link {
  color: var(--color-text-secondary) !important;
  font-weight: 500;
  transition: all 0.3s ease;
  border-radius: var(--radius-sm);
  padding: 0.5rem 1rem;
}

.navbar-nav .nav-link:hover {
  color: var(--color-primary) !important;
  background: rgba(99, 102, 241, 0.1);
}

.navbar-nav .nav-link.active {
  color: var(--color-primary) !important;
  background: rgba(99, 102, 241, 0.2);
}

/* Forms */
.form-control {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: var(--radius-md);
  color: var(--color-text-primary);
  padding: var(--spacing-sm);
  transition: all 0.3s ease;
}

.form-control:focus {
  background: rgba(255, 255, 255, 0.1);
  border-color: var(--color-primary);
  box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.1);
  color: var(--color-text-primary);
}

.form-control::placeholder {
  color: var(--color-text-muted);
}

/* Badges */
.badge {
  padding: 0.25rem 0.75rem;
  border-radius: var(--radius-sm);
  font-weight: 600;
  font-size: 0.75rem;
}

.badge-primary {
  background: var(--gradient-primary);
  color: white;
}

.badge-success {
  background: var(--color-success);
  color: white;
}

.badge-warning {
  background: var(--color-warning);
  color: white;
}

.badge-danger {
  background: var(--color-danger);
  color: white;
}

/* Alerts */
.alert {
  border: none;
  border-radius: var(--radius-md);
  padding: var(--spacing-md);
  margin-bottom: var(--spacing-md);
}

.alert-primary {
  background: rgba(99, 102, 241, 0.1);
  color: var(--color-primary);
  border-left: 4px solid var(--color-primary);
}

.alert-success {
  background: rgba(16, 185, 129, 0.1);
  color: var(--color-success);
  border-left: 4px solid var(--color-success);
}

.alert-warning {
  background: rgba(245, 158, 11, 0.1);
  color: var(--color-warning);
  border-left: 4px solid var(--color-warning);
}

.alert-danger {
  background: rgba(239, 68, 68, 0.1);
  color: var(--color-danger);
  border-left: 4px solid var(--color-danger);
}

/* Utilities */
.text-primary { color: var(--color-primary) !important; }
.text-secondary { color: var(--color-secondary) !important; }
.text-success { color: var(--color-success) !important; }
.text-warning { color: var(--color-warning) !important; }
.text-danger { color: var(--color-danger) !important; }
.text-muted { color: var(--color-text-muted) !important; }

.bg-primary { background: var(--color-primary) !important; }
.bg-secondary { background: var(--color-secondary) !important; }
.bg-success { background: var(--color-success) !important; }
.bg-warning { background: var(--color-warning) !important; }
.bg-danger { background: var(--color-danger) !important; }

/* Animations */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes slideIn {
  from { transform: translateX(-100%); }
  to { transform: translateX(0); }
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

.fade-in {
  animation: fadeIn 0.6s ease-out;
}

.slide-in {
  animation: slideIn 0.6s ease-out;
}

.pulse {
  animation: pulse 2s infinite;
}

/* Responsive Design */
@media (max-width: 768px) {
  h1 { font-size: 2rem; }
  h2 { font-size: 1.75rem; }
  h3 { font-size: 1.5rem; }
  
  .glass-card {
    margin: var(--spacing-sm);
  }
  
  .btn {
    width: 100%;
    margin-bottom: var(--spacing-sm);
  }
}

@media (max-width: 576px) {
  :root {
    --spacing-md: 1rem;
    --spacing-lg: 1.5rem;
  }
  
  .card-body {
    padding: var(--spacing-sm);
  }
}

/* Dark mode support */
@media (prefers-color-scheme: dark) {
  :root {
    --color-bg-primary: #0F172A;
    --color-bg-secondary: #1E293B;
  }
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--color-bg-secondary);
}

::-webkit-scrollbar-thumb {
  background: var(--color-primary);
  border-radius: var(--radius-sm);
}

::-webkit-scrollbar-thumb:hover {
  background: var(--color-secondary);
} 